import React, { createContext, useState, useEffect } from "react";

// Crer le contexte
export const UserContext = createContext(null);

export const UserProvider = ({ children }) => {
  const [user, setUser] = useState(null);

  // Rcuprer les infos utilisateur depuis localStorage au chargement de l'app
  useEffect(() => {
    const storedUser = localStorage.getItem("user");
    if (storedUser) {
      setUser(JSON.parse(storedUser)); // Dfinir l'utilisateur depuis localStorage
    }
  }, []);

  // Fonction pour mettre  jour l'utilisateur (lors de la connexion ou dconnexion)
  const updateUser = (userInfo) => {
    setUser(userInfo);
    if (userInfo) {
      localStorage.setItem("user", JSON.stringify(userInfo));
    } else {
      localStorage.removeItem("user");
    }
  };

  return (
    <UserContext.Provider value={{ user, updateUser }}>
      {children}
    </UserContext.Provider>
  );
};
